import React,{Component} from 'react'
import Input from '../../components/UI/inputForm/input';
import Button from '../../components/UI/Button/Button';

import classes from './AuthLogin.css'

import {connect} from 'react-redux'

import * as actions from '../../store/action/index'
import Spinner from '../../components/UI/Spinner/Spinner'

import {Redirect} from 'react-router-dom'
//import BurgerBuilder from '../BurgerBuilder/BurgerBuilder';

class SignUp extends Component{

state={
    controller:{
        email:{
            elementType:'input',
            elementConfig:{
                type:'email',
                placeholder:'Enter Email.'
            },
            value:'',

            validation:{
                required: true
            },
            valid:false,
            touched:false
        },

        password:{
            elementType:'input',
            elementConfig:{
                type:'password',
                placeholder:'Enter Password'
            },
            value:'',
            validation:{
                required:true
            },
            valid:false,
            touched:false
        }
    },

    isSignUp:true
}



onChangeHandler=(event,controllerName)=>{
    


    const updatedControls={
        ...this.state.controller,

        [controllerName]:{
            ...this.state.controller[controllerName],
            value:event.target.value,
            // touched:true,
            valid:this.validationHandler(event.target.value,this.state.controller[controllerName].validation)
        },
       
    
       
    }
    this.setState({controller:updatedControls})


    const updateCTR={
        ...this.state.controller,
    
    }
    
    const updatedorderEle={
        ...updateCTR[controllerName]
    }
    
    
    console.log("loginformTouched="+updatedorderEle.touched);


    if(updatedorderEle.value===" "){
        updatedorderEle.touched=true
    }else{
        updatedorderEle.touched=false

    }

}


validationHandler=(value,rules)=>{
    // console.log("req="+value.required)
        let isValid = true;
            
                    if (rules.required) {
                        isValid = value.trim() !== '' && isValid;
                    }
            
                    if (rules.minLength) {
                        isValid = value.length >= rules.minLength && isValid
                    }
            
                    if (rules.maxLength) {
                        isValid = value.length <= rules.maxLength && isValid
                    }
            
                    if(rules.isNumeric){
    
                        const pattern=/^\d+$/;
                        isValid=pattern.test(value) && isValid
                    }
    
            return isValid;
    
    }

    formHandler=(event)=>{
        console.log("buttonClicked")
        event.preventDefault();

        this.props.onAuth(this.state.controller.email.value,this.state.controller.password.value,this.state.isSignUp)


    }

    SignUpchangeHandler=()=>{

            this.setState(prevState=>{
                return {isSignUp:!prevState.isSignUp}
            })
console.log("Signup="+this.state.isSignUp)
    }

componentDidMount(){
    if(this.props.burgerBuilding===false && this.props.authRedirct !== '/'){
        this.props.onSetAuthRedirct();
    }
}

    render(){
        let logindata=[];

        for(var key in this.state.controller){
            logindata.push({
                id:key,
                config:this.state.controller[key]
            })

        }



    let formData=logindata.map(formElement=>{
            return <Input 
                        key={formElement.id}
                        ElementType={formElement.config.elementType}
                        ElementConfig={formElement.config.elementConfig}
                        value={formElement.config.value}
                        invalid={!formElement.config.valid}
                        shouldValidate={formElement.config.validation}
                        touched={formElement.config.touched}
                        changed={(event)=>this.onChangeHandler(event,formElement.id)}
                    />
    })

    if(this.props.loading){
        formData=<Spinner></Spinner>
    }
        

    let errorMessgae=null

    if(this.props.error){
        errorMessgae=this.props.error.message
    }

    let authRedirect=null;

    if(this.props.isAuthenticated){
        authRedirect=<Redirect to={this.props.authRedirct}/>
    }

        return(
            <div className={classes.AuthLogin}>

                {authRedirect}
                {errorMessgae}


                <form onSubmit={this.formHandler}>

                    {formData}

                    <Button btnType="Success" >SUBMIT</Button>
                    </form>
                    
            <br/>

            <Button btnType='Success' clicked={this.SignUpchangeHandler}> 
        
        {this.state.isSignUp?'Sign_In':'Sign_Up'}</Button>
        
            </div>

        )
    }
    
}

const mapStateToProps=(state)=>{
    return{

        loading:state.auth.loading,
        error:state.auth.error,
        isAuthenticated:state.auth.token != null,
        burgerBuilding:state.BurgerReducer.building,
        authRedirct:state.auth.authRedirect
    }
}


const mapDispatchToProps=(dispatch)=>{
    return{
        onAuth:(email,password,isSignUp)=>dispatch(actions.authAsyncSucces(email,password,isSignUp)) ,   
        onSetAuthRedirct:()=>dispatch(actions.setAuthRedirect("/"))
    }
}



export default connect(mapStateToProps,mapDispatchToProps)(SignUp);


